.monopoly-button{
    border: none;
    background-color:#C70000;
    color:white;
    border:1px solid white;
    border-radius:6px;
    cursor: pointer;
    min-width:100px;
    font-size:20px;
    padding:5px;
}
.monopoly-button:disabled{
    cursor:not-allowed;
    opacity:0.6
}

